      padding-right: 0; } } }




@media (max-width: 991px) {
  .menu {
    &_item {
      padding-right: 9px; }
    &_link {
      font-size: 10px; } }
  .subheader {
    &_logo {
      img {
        width: 100%; } }
    &_official {
      font-size: 11px; }
    &_phone {
      font-size: 18px; }
    &_btn {
      width: 100%;
      height: 40px;
      margin-top: 21px; } }
  .mobile {
    &_item {
      width: 100%; } }
  .promo {
    &_header {
      font-size: 40px; }
    &_subheader {
      font-size: 30px; } }
  .reasons {
    &_block {
      flex-direction: column;
      align-items: center; }
    &_round {
      margin-right: 0; }
    &_descr {
      width: 300px;
      margin-top: 20px; } }
  .choice {
    &_img {
      width: 100%;
      height: 100%; } }
  .footer {
    &_wrapper {
      flex-direction: column;
      align-items: center;
      padding-top: 24px; }
    &_social {
      width: 100%;
      justify-content: center; }
    &_links {
      width: 100%;
      padding-top: 20px;
      &_main>a {
        font-size: 13px; }
      &_sub>a {
        font-size: 11px; } }
    &_mobile {
      padding-top: 10px; } } }



@media (max-width: 767px) {
  .menu {
    position: fixed;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    top: 0;
    left: -100%;
    width: 50%;
    height: 100vh;
    padding: 20px 15px;
    background-color: rgba(0,0,0, .8);
    transition: 0.6s all;
    &_active {
      left: 0; }
    &_item {
      padding-right: 0;
      border: none;
      margin: 10px 0; }
    &_link {
      font-size: 20px; } }

  .hamburger {
    display: block;
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    width: 26px;
    height: 12px;
    span {
      display: block;
      height: 2px;
      width: 100%;
      background-color: white;
      margin-bottom: 2px;
      transition: 0.5s all; }
    &_active {
      span {
        margin-bottom: -2px;
        &:nth-child(1) {
          transform: translateY(3px) rotate(-45deg); }
        &:nth-child(2) {
          display: none; }
        &:nth-child(3) {
          transform: translateY(3px) rotate(45deg); } } } }
  .xs-hidden {
    display: none; }
  .xs-visible {
    display: block; }

  .subheader {
    &_phone {
      display: block;
      margin-top: 10px;
      color: #ffffff;
      font-size: 16px;
      font-weight: 400;
      text-transform: uppercase;
      text-align: center; }
    &_btn {
      display: block;
      width: 200px;
      height: 40px;
      margin: 0 auto;
      margin-top: 10px; } }

  .promo {
    &_header {
      line-height: 45px; } }

  .footer {
    &_wrapper {
      flex-direction: column;
      align-items: center;
      padding-top: 24px; }
    &_social {
      width: 100%;
      justify-content: center; }
    &_links {
      width: 100%;
      padding-top: 20px;
      &_main>a {
        font-size: 13px; }
      &_sub>a {
        font-size: 9px; } }
    &_mobile {
      padding-top: 10px; } } }
.footer_wrapper > div:nth-child(3) {
  justify-content: center; }

